<template>
  <div id="withDraw">
    <common-header title="提现"></common-header>
    <div class="withDraw__form">
      <!-- <div class="form">
        <span>银行卡</span>
        <span>建设银行储蓄卡(888)</span>
      </div>
      <div class="form">
        <span>支付密码</span>
        <input type="text" placeholder="请输入支付密码">
      </div>
      <div class="form">
        <span>金额(￥)</span>
        <input type="text" placeholder="请输入提现的金额">
      </div> -->
      <Cell title="留言" value="建设银行储蓄卡(8888)" @click="linkTo('BankCard')"/>
      <Cell title="支付密码" type="password" v-model="price" placeholder="请输入支付密码"/>
      <Cell class="border" title="金额(￥)" type="number" placeholder="请输入提现的金额"/>
      <div class="form__info">
        <span>当前零钱余额256.0元</span>
        <span class="withDraw">全部提现</span>
      </div>
      <div class="form__btn" @click="linkTo('Phone')">下一步</div>
      <div class="form__tips">
        <div class="tips__title">
          提现说明
        </div>
        <div class="tips__info">如果你无法简洁的表达你的想法，那只说明你还不够了解它。</div>
      </div>
    </div>
  </div>  
</template>

<script>
  import Cell from '@/components/Cell/Cell';

  export default {
    data() {
      return {
        price: ''
      }
    },
    mounted() {

    },
    components: {
      Cell
    }
  }
</script>


<style lang="scss">
  #withDraw {
    .withDraw__form {
      // .form {
      //   display: flex;
      //   justify-content: space-between;
      //   height: 1.2rem;
      //   line-height: 1.2rem;

      //   input {
      //     text-align: right;
      //   }
      // }

      .border {
        border-bottom: 1px solid #eee;
      }

      .form__info {
        height: 1.0667rem;
        line-height: 1.0667rem;
        font-size: 0.373rem;
        padding: 0 0.4rem;
        color: #333;

        .withDraw {
          padding-left: 0.2rem;
          color: #de7fef;
        }
      }

      .form__btn {
        margin: 0.667rem 0.4rem;
        width: calc(100% - 0.8rem);
        height: 1.2rem;
        line-height: 1.1733rem;
        font-size: 0.493rem;
        text-align: center;
        color: #fff;
        border-radius: 1.2rem;
        background: #ccc;
      }

      .form__tips {
        padding: 0 0.4rem;
        color: #333;

        .tips__title {
          font-size: 0.4rem;
          padding-bottom: 0.1rem;
          font-weight: 600;
        }
      }
    }
  }
</style>



